@import './Components/Base';

html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    box-sizing: border-box;

    *, * * {
        box-sizing: inherit;
    }
}

.App {
    background-color: var(--background-one);
}